
@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@800&display=swap");

#pageAccueil {
  background-image: url("../img/road.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: white;
  min-height: 10vh;
  background-attachment: fixed /*Pour l'effet*/; }


h2 {
  font-family: 'Orbitron', sans-serif; }

/*Hauteur*/
$hauteurs: 5, 25, 50, 75;
@each $hauteur in $hauteurs {
  .haut-#{$hauteur} {
    height: $hauteur + vh; } }

/*Hauteur du texte *************************************/
$fontSizes: ("small": 1, "medium": 1.5, "large": 2, "xlarge": 3);
@each $grandeur, $valeur in $fontSizes {
  .font-#{$grandeur} {
    font-size: $valeur + rem; } }

.accueilImg {
  object-fit: cover;
  min-height: 50vh; }


